<template>
  <div class="home">
    <Swiper />
    <div class="section" v-for="(item, index) in songList" :key="index">
      <div class="section-title">{{ item.name }}</div>
      <content-list :contentList="item.list"></content-list>
    </div>
  </div>
</template>

<script>
import Swiper from "../components/Swiper.vue";
import ContentList from "../components/ContentList.vue";
import { getAllSinger, getAllSongList } from "../api/index";

export default {
  name: "home",
  components: {
    Swiper: Swiper,
    ContentList
  },
  data() {
    return {
      songList: [
        {
          name: "歌单",
          list: []
        },
        {
          name: "歌手",
          list: []
        }
      ]
    };
  },
  created() {
    this.getSongList();
    this.getSinger();
  },
  mounted() {
    console.log("测试");
    console.log(window.sessionStorage.getItem("isApply"));

    if (!window.sessionStorage.getItem("isApply")) {
      this.$alert(
        '点击<strong>高级->坚持访问</strong>允许访问以获得完整体验 <img src="src/assets/img/tubiao.jpg" alt="" />',
        "初次访问提示",
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: "去允许",
          callback: () => {
            window.open("https://8.148.6.61:8888");
            window.sessionStorage.setItem("isApply", true);
            // this.$store.commit("isApply", true);
          }
        }
      );
    }
  },
  methods: {
    // 获取前十条歌单
    getSongList() {
      getAllSongList()
        .then(res => {
          this.songList[0].list = res.slice(0, 10);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 获取前十名歌手
    getSinger() {
      getAllSinger()
        .then(res => {
          this.songList[1].list = res.slice(0, 10);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/home.scss";
</style>



